import React from "react";
import { Button } from "@mui/material";

import ImproveImg from "../assets/improve.svg";
import InsightsImg from "../assets/insights.svg";
import TestImg from "../assets/test.svg";
import CustomizationImg from "../assets/customization.svg";

const Customization = ({ className, openCallModal }) => {
  return (
    <section className={className}>
      <div className="info-blocks">
        {[
          {
            iconImg: ImproveImg,
            title: "Improve on the Go",
            desc: "Observe communication between the bot and the user and improvise on the go.",
          },
          {
            iconImg: InsightsImg,
            title: "Get Critical Insights",
            desc: "Analyze trends, demand and customer behavior, when you capture data from user.",
          },
          {
            iconImg: TestImg,
            title: "Test and Deploy",
            desc: "Create different versions of your chat flow. Tweak until it is ready to show to the world.",
          },
        ].map(({ iconImg, title, desc, contactLink }, index) => (
          <div className="block" key={index}>
            {iconImg && (
              <div className="icon">
                <img src={iconImg} alt="Icon" />
              </div>
            )}
            <div className="info">
              {title && <h4 className="title">{title}</h4>}
              {desc && <p className="desc">{desc}</p>}
              {contactLink && (
                <a href={`mailto:${contactLink}`} className="link">
                  {contactLink}
                </a>
              )}
            </div>
          </div>
        ))}
      </div>
      <div className="content">
        <div className="block">
          <div className="col-left">
            <img src={CustomizationImg} alt="New Customer" />
          </div>
          <div className="col-right">
            <div className="info">
              <span>{"ENTERPRISE GRADE SOLUTIONS"}</span>
              <h2>{"End-to-end Customizations for Large Enterprises."}</h2>
              <p>
                {
                  "Large organization require unique solutions that are capable to handle huge number of interactions with security compliance and long list of integrations. At EasySocial, we value your uniqueness and are fully capable of delivering end to end integrated solutions that not only enhance your productivity but also puts you at the fore front of technology."
                }
              </p>
              <div className="actions">
                <Button
                  variant="contained"
                  className="demo"
                  onClick={openCallModal}
                >
                  {"Talk to an Expert"}
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Customization;
