import React from "react";

import LeadsImg from "../assets/leads.svg";
import BroadcastImg from "../assets/broadcast.svg";
import NotificationsImg from "../assets/notifications.svg";

const ExistingCustomer = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <div className="info-blocks">
          {[
            {
              featImg: LeadsImg,
              lead: "AUTO ASSIGN LEADS",
              title: "Imagine the power of teamwork.",
              desc: "Auto assign enquiries to different team members based on predefined rules. Lets say, enquires intend for sales will go to John and enquiries made for customer support will go to Natalie.",
            },
            {
              featImg: BroadcastImg,
              lead: "BROADCAST MESSAGES",
              title: "Broadcast messages, reach new customers.",
              desc: "Reach new audience on WhatsApp with a tempting offer or an introduction to your product. Simply upload a database of phone numbers and start reaching potential customers in no time.",
            },
            {
              featImg: NotificationsImg,
              lead: "PERSONALIZED NOTIFICATIONS",
              title: "Re-think Notifications that can drive sales.",
              desc: "Based on the unique interaction with each customer and their purchasing behaviour, you can send personalized notifications for dropped cart, shipping status, discount codes and more.",
            },
          ].map(({ featImg, lead, title, desc }, index) => (
            <div className="block" key={index}>
              <div className="image">
                <img src={featImg} alt="Feature" />
              </div>
              <div className="info">
                {lead && <span className="lead">{lead}</span>}
                {title && <h4 className="title">{title}</h4>}
                {desc && <p className="desc">{desc}</p>}
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ExistingCustomer;
