import React, { Fragment, useEffect, useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react";

import useOnScreen from "../../../../hooks/useOnScreen";

import eMediaImg from "../assets/e-media.svg";
import mediaVid from "../assets/media.mp4";
import buttonVid from "../assets/button.mp4";
import docVid from "../assets/doc.mp4";
import responseVid from "../assets/response.mp4";
import listVid from "../assets/list.mp4";
import mapVid from "../assets/map.mp4";

const Engaging = ({ className, isTablet }) => {
  const [currentMedia, setCurrentMedia] = useState(eMediaImg),
    [currentMediaType, setCurrentMediaType] = useState("img"),
    [currentMediaKey, setCurrentMediaKey] = useState(""),
    [loadedOnce, setLoadedOnce] = useState(false),
    mediaRef = useRef(),
    autoPlayRef = useRef(),
    visible = useOnScreen(mediaRef, "0px", 0.6);

  useEffect(() => {
    !loadedOnce && setLoadedOnce(visible);
    if (autoPlayRef.current)
      !loadedOnce && visible && autoPlayRef.current.click();
  }, [loadedOnce, visible]);

  return (
    <section className={className}>
      <div className="content">
        <div className="entry">
          <span className="lead">{"BEYOND TEXT MESSAGING"}</span>
          <h4 className="title">
            {"Future of Messaging,"}
            <br />
            {"Faster & More Engaging"}
          </h4>
          <p className="desc">
            {
              "WhatsApp business API enabled ChatBot is the right partner for your business. It lets you communicate in ways that were never possible before. Engage better to build lasting relationships with your customers."
            }
          </p>
        </div>
        <div className="info-blocks">
          <div className="block media" ref={mediaRef}>
            {currentMediaType === "img" && (
              <img src={currentMedia} alt="E-Media" />
            )}
            {currentMediaType === "video" && (
              <video
                src={currentMedia}
                autoPlay={true}
                muted={true}
                controls={false}
              />
            )}
          </div>
          {!isTablet && (
            <Fragment>
              <div className="block controls-s">
                <div className="control-wrapper">
                  <button
                    type="button"
                    className={`control${
                      currentMediaKey === "media" ? " active" : ""
                    }`}
                    ref={autoPlayRef}
                    onClick={() => {
                      setCurrentMediaKey("media");
                      setCurrentMediaType("video");
                      setCurrentMedia(mediaVid);
                    }}
                  >
                    <h5>{"Image & Videos"}</h5>
                    <p>
                      {
                        "Showcase product images & videos to create a marked impression."
                      }
                    </p>
                  </button>
                  <button
                    type="button"
                    className={`control${
                      currentMediaKey === "button" ? " active" : ""
                    }`}
                    onClick={() => {
                      setCurrentMediaKey("button");
                      setCurrentMediaType("video");
                      setCurrentMedia(buttonVid);
                    }}
                  >
                    <h5>{"Buttons"}</h5>
                    <p>
                      {
                        "Give more convenience and ease of access by introducing buttons for quick reply's or links to external URLs."
                      }
                    </p>
                  </button>
                  <button
                    type="button"
                    className={`control${
                      currentMediaKey === "doc" ? " active" : ""
                    }`}
                    onClick={() => {
                      setCurrentMediaKey("doc");
                      setCurrentMediaType("video");
                      setCurrentMedia(docVid);
                    }}
                  >
                    <h5>{"PDFs & Documents"}</h5>
                    <p>
                      {
                        "Send Invoices, Brochures, Tickets, Reports or other documents to the chat. Several popular formats supported."
                      }
                    </p>
                  </button>
                </div>
              </div>
              <div className="block controls-e">
                <div className="control-wrapper">
                  <button
                    type="button"
                    className={`control${
                      currentMediaKey === "res" ? " active" : ""
                    }`}
                    onClick={() => {
                      setCurrentMediaKey("res");
                      setCurrentMediaType("video");
                      setCurrentMedia(responseVid);
                    }}
                  >
                    <h5>{"Dynamic Response"}</h5>
                    <p>
                      {
                        "Program replies with a numeric indicator or a phrase to give access to faster navigation."
                      }
                    </p>
                  </button>
                  <button
                    type="button"
                    className={`control${
                      currentMediaKey === "list" ? " active" : ""
                    }`}
                    onClick={() => {
                      setCurrentMediaKey("list");
                      setCurrentMediaType("video");
                      setCurrentMedia(listVid);
                    }}
                  >
                    <h5>{"Lists"}</h5>
                    <p>
                      {
                        "When you have several options to choose from, allow the user to select an option from a pop-up list of items."
                      }
                    </p>
                  </button>
                  <button
                    type="button"
                    className={`control${
                      currentMediaKey === "loc" ? " active" : ""
                    }`}
                    onClick={() => {
                      setCurrentMediaKey("loc");
                      setCurrentMediaType("video");
                      setCurrentMedia(mapVid);
                    }}
                  >
                    <h5>{"Geo Location"}</h5>
                    <p>
                      {
                        "Send and receive location coordinates. Share location of your store, or receive location for delivery."
                      }
                    </p>
                  </button>
                </div>
              </div>
            </Fragment>
          )}
        </div>
        {isTablet && (
          <div className="slider-container">
            <Swiper
              spaceBetween={0}
              slidesPerView={1.55}
              loop={true}
              breakpoints={{
                585: {
                  slidesPerView: 2.7,
                },
                768: {
                  slidesPerView: 2.7,
                },
              }}
            >
              <SwiperSlide>
                <button
                  type="button"
                  className={`control${
                    currentMediaKey === "media" ? " active" : ""
                  }`}
                  ref={autoPlayRef}
                  onClick={() => {
                    setCurrentMediaKey("media");
                    setCurrentMediaType("video");
                    setCurrentMedia(mediaVid);
                  }}
                >
                  <h5>{"Image & Videos"}</h5>
                  <p>
                    {
                      "Showcase product images & videos to create a marked impression."
                    }
                  </p>
                </button>
              </SwiperSlide>
              <SwiperSlide>
                <button
                  type="button"
                  className={`control${
                    currentMediaKey === "button" ? " active" : ""
                  }`}
                  onClick={() => {
                    setCurrentMediaKey("button");
                    setCurrentMediaType("video");
                    setCurrentMedia(buttonVid);
                  }}
                >
                  <h5>{"Buttons"}</h5>
                  <p>
                    {
                      "Give more convenience and easy of access by introducing buttons for quick reply's or links to external URLs."
                    }
                  </p>
                </button>
              </SwiperSlide>
              <SwiperSlide>
                <button
                  type="button"
                  className={`control${
                    currentMediaKey === "doc" ? " active" : ""
                  }`}
                  onClick={() => {
                    setCurrentMediaKey("doc");
                    setCurrentMediaType("video");
                    setCurrentMedia(docVid);
                  }}
                >
                  <h5>{"PDFs & Documents"}</h5>
                  <p>
                    {
                      "Send Invoices, Brochures, Tickets, Reports or other documents to the chat. Several popular formats supported."
                    }
                  </p>
                </button>
              </SwiperSlide>
              <SwiperSlide>
                <button
                  type="button"
                  className={`control${
                    currentMediaKey === "res" ? " active" : ""
                  }`}
                  onClick={() => {
                    setCurrentMediaKey("res");
                    setCurrentMediaType("video");
                    setCurrentMedia(responseVid);
                  }}
                >
                  <h5>{"Dynamic Response"}</h5>
                  <p>
                    {
                      "Program replies with a numeric indicator or a phrase to give access to faster navigation."
                    }
                  </p>
                </button>
              </SwiperSlide>
              <SwiperSlide>
                <button
                  type="button"
                  className={`control${
                    currentMediaKey === "list" ? " active" : ""
                  }`}
                  onClick={() => {
                    setCurrentMediaKey("list");
                    setCurrentMediaType("video");
                    setCurrentMedia(listVid);
                  }}
                >
                  <h5>{"Lists"}</h5>
                  <p>
                    {
                      "When you have several options to choose from, allow the user to select an option from a pop-up list of items."
                    }
                  </p>
                </button>
              </SwiperSlide>
              <SwiperSlide>
                <button
                  type="button"
                  className={`control${
                    currentMediaKey === "loc" ? " active" : ""
                  }`}
                  onClick={() => {
                    setCurrentMediaKey("loc");
                    setCurrentMediaType("video");
                    setCurrentMedia(mapVid);
                  }}
                >
                  <h5>{"Geo Location"}</h5>
                  <p>
                    {
                      "Send and receive location coordinates. Share location of your store, or receive location for delivery."
                    }
                  </p>
                </button>
              </SwiperSlide>
            </Swiper>
          </div>
        )}
      </div>
    </section>
  );
};

export default Engaging;
