import React from "react";

import ConversationMdImg from "../assets/conversation-md.svg";
import ConversationImg from "../assets/conversation.svg";

const Conversation = ({ className, isMobile }) => {
  return (
    <section className={className}>
      <div className="info-blocks">
        <div className="block">
          <div className="info">
            <span className="lead">{"NO CODE CHATBOT BUILDER"}</span>
            <h4 className="title">
              {"Design your conversation with complete flexibility."}
            </h4>
            <div className="desc">
              <p>
                {
                  "Experience revolutionary node based chat builder that lets you create conversations in minutes. With its advanced features, go creative and deliver intuitive conversational experience to your customers."
                }
              </p>
              <p>
                {
                  "Capture actionable customer information that assists in sales & support."
                }
              </p>
            </div>
          </div>
          <div className="image">
            <img
              src={isMobile ? ConversationImg : ConversationMdImg}
              alt="Conversation"
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default Conversation;
