import React, { Fragment, useState } from "react";
// react-helmet
import { Helmet } from "react-helmet";
// material-ui imports
import { useMediaQuery } from "@material-ui/core";
// theme
import { useTheme } from "@material-ui/core/styles";

import Header from "../components/Header";
import Footer from "../components/Footer";
import PageHeader from "./PageHeader";
import Unprecedented from "./Unprecedented";
import Engaging from "./Engaging";
import MidBanner from "./MidBanner";
import Features from "./Features";
import Conversation from "./Conversation";
import Customization from "./Customization";
import BannerBottom from "./BannerBottom";
import PopUpCall from "../components/PopUpCall";
import EnterprisePopUp from "../components/EnterprisePopUp";

// styles
import "react-phone-input-2/lib/material.css";
import "swiper/swiper-bundle.min.css";
import useStyles from "./styles/muiStyles";

const Engage = () => {
  const classes = useStyles(),
    // theme setup
    theme = useTheme(),
    // media query breakpoints
    isMobile = useMediaQuery(theme.breakpoints.down("sm")),
    isTablet = useMediaQuery(theme.breakpoints.down("md")),
    [callModal, setCallModal] = useState(false),
    [enterpriseModal, setEnterpriseModal] = useState(false),
    openEnterpriseModal = () => setEnterpriseModal(true),
    closeEnterpriseModal = (event, reason) => {
      setEnterpriseModal(false);
    },
    openCallModal = () => setCallModal(true),
    closeCallModal = (event, reason) => {
      setCallModal(false);
    };

  return (
    <Fragment>
      {/* Head */}
      <Helmet>
        <title>Engage - EasySocial.io</title>
        <link rel="canonical" href="https://easysocial.io/" />
        <meta
          name="keywords"
          content="Whatsapp chatbot ,automatic WhatsApp for business, Whatsapp AI, customizable whatsapp"
        />
        <meta
          name="description"
          content="EasySocial offers customizable and affordable WhatsApp chatbot solution for quick and hassle free functioning of your business."
        />
      </Helmet>
      {/* <Header /> */}
      <Header />
      {/* PageHeader */}
      <PageHeader
        isMobile={isMobile}
        className={classes["page-header"]}
        openCallModal={openCallModal}
      />
      {/* Unprecedented */}
      <Unprecedented className={classes.unprecedented} />
      {/* Engaging */}
      <Engaging className={classes.engaging} isTablet={isTablet} />
      {/* MidBanner */}
      <MidBanner
        className={classes["mid-banner"]}
        openCallModal={openCallModal}
      />
      {/* Features */}
      <Features className={classes.features} />
      {/* Conversation */}
      <Conversation isMobile={isMobile} className={classes.conversation} />
      {/* Customization */}
      <Customization
        className={classes.customization}
        openCallModal={openEnterpriseModal}
      />
      {/* Mid Bottom */}
      <BannerBottom className={classes["banner-bottom"]} />
      {/* PopUpCall */}
      <PopUpCall open={callModal} onClose={closeCallModal} />
      {/* EnterprisePopUp */}
      <EnterprisePopUp open={enterpriseModal} onClose={closeEnterpriseModal} />
      {/* Footer */}
      <Footer />
    </Fragment>
  );
};

export default Engage;
