import React from "react";

import AvailableImg from "../assets/available.svg";
import GlobalImg from "../assets/global.svg";
import MinuteImg from "../assets/minute.svg";
import MultilingualImg from "../assets/multilingual.svg";
import MediaImg from "../assets/media.svg";
import HumanTouchImg from "../assets/human-touch.svg";

const ExistingCustomer = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <div className="entry">
          <span>{"UNPRECEDENTED FEATURES"}</span>
          <h2>
            {"Unmatched Customer Engagement,"}
            <br />
            {"Zero Compromises."}
          </h2>
        </div>
        <div className="info-blocks">
          {[
            {
              iconImg: AvailableImg,
              title: "Availibility",
              desc: "Become a proactive brand.  Your availibilty gives you an edge and better chance on closing deals.",
            },
            {
              iconImg: GlobalImg,
              title: "Global Users",
              desc: "Your customers dont need to install new apps to be connected with you. Reach out to your audience globally.",
            },
            {
              iconImg: MinuteImg,
              title: "Chats/minute",
              desc: "Manage simultaneous conversations unlike a call center. Save upto 80% manpower.",
            },
            {
              iconImg: MultilingualImg,
              title: "Multilingual",
              desc: "Cater to your customers in a language they prefer. All major global languages are supported.",
            },
            {
              iconImg: MediaImg,
              title: "Rich Media",
              desc: "Communicate your brand and products more effectively with multimedia graphics & more.",
            },
            {
              iconImg: HumanTouchImg,
              title: "Human Touch",
              desc: "Seamlessly hand over of conversations to a human agent for a more personalized experience.",
            },
          ].map(({ iconImg, title, desc, contactLink }, index) => (
            <div className="block" key={index}>
              {iconImg && (
                <div className="icon">
                  <img src={iconImg} alt="Icon" />
                </div>
              )}
              <div className="info">
                {title && <h4 className="title">{title}</h4>}
                {desc && <p className="desc">{desc}</p>}
                {contactLink && (
                  <a href={`mailto:${contactLink}`} className="link">
                    {contactLink}
                  </a>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ExistingCustomer;
