import React from "react";
import { Button } from "@mui/material";

import EngageMain from "../assets/engage-main.png";
import EngageMainMd from "../assets/engage-main-md.png";

const Engage = ({ isMobile, className, openCallModal }) => {
  return (
    <section className={className}>
      <div className="content">
        <div className="intro">
          <div className="data">
            <div className="description">
              <h2 className="title">
                {"WhatsApp Chatbot for Customer Support, Sales & Marketing."}
              </h2>
              <p className="info">
                {
                  "With WhatsApp Business API and our powerful ChatBot soultion get ready to embrace your customers with automated support, sales and marketing!"
                }
              </p>
              <div className="actions">
                <Button variant="contained" href="/pricing" className="action">
                  {"Explore Plans"}
                </Button>
                <Button
                  variant="outlined"
                  className="action-alt"
                  onClick={openCallModal}
                >
                  {"Talk to Sales"}
                </Button>
              </div>
              <ul className="features">
                <li>{"Free tools with a lot of features"}</li>
                <li>{"No credit card required"}</li>
              </ul>
            </div>
          </div>
          <div className="image">
            <img
              src={isMobile ? EngageMain : EngageMainMd}
              alt="Feature-Engage"
              className="feature-image"
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default Engage;
